@media only screen and (max-width: 575px) {
  html {
    font-size: 14px; } }

a {
  text-decoration: none !important; }

.App {
  justify-content: center;
  display: flex; }

.App_Cover {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column; }

.Cover {
  display: flex;
  width: 90%;
  margin: auto;
  justify-content: center;
  flex-direction: column; }

.All_Movies {
  flex: 1;
  max-width: 1250px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 95%; }

@media only screen and (min-width: 1024px) and (max-width: 1250px) {
  .All_Movies {
    width: 1001px; } }

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .All_Movies {
    width: 750px; } }

@media only screen and (min-width: 575px) and (max-width: 768px) {
  .All_Movies {
    width: 501px; } }

@media only screen and (max-width: 575px) {
  .All_Movies {
    width: 250px; } }

@media only screen and (max-width: 575px) {
  .All_Movies {
    justify-content: center; } }

.Single_Movie {
  display: flex;
  flex-direction: column;
  width: 240px;
  margin: 10px 5px;
  text-align: center; }

.Single_Movie_Name {
  width: 100%;
  align-content: center;
  display: flex;
  height: 50px;
  text-align: center; }

.Single_Movie_Name div {
  display: flex;
  margin: auto; }

.Single_Movie_Bottom {
  display: flex;
  justify-content: space-evenly; }

.Single_Movie_Edit {
  display: flex;
  flex-direction: row;
  width: 95%;
  margin: 10px auto;
  justify-content: center; }

@media only screen and (max-width: 768px) {
  .Single_Movie_Edit {
    flex-direction: column; } }

@media only screen and (max-width: 768px) {
  .Single_Movie_Edit {
    align-items: center; } }

.Single_Movie_Edit img {
  width: 300px;
  height: 425px; }

.Single_Movie_Edit div:first-child {
  display: flex;
  flex-direction: column; }

.Single_Movie_Description {
  margin-left: 20px;
  max-width: 700px;
  text-align: justify; }

@media only screen and (max-width: 768px) {
  .Single_Movie_Description {
    margin-left: 0; } }

.Edit_Movie {
  width: 95%;
  max-width: 1020px;
  margin: auto; }

.Header_Page {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 70px; }

.Header_Page h2 {
  margin: 0; }

.Header_Page .Header_Page_Cover {
  width: 95%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; }

@media only screen and (max-width: 585px) {
  .Header_Page .Header_Page_Cover {
    flex-direction: column; } }

@media only screen and (max-width: 585px) {
  .Header_Page .Header_Page_Cover {
    align-items: flex-start; } }

.Header_Page .Header_Page_Cover_Left {
  width: 45%;
  min-width: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

@media only screen and (max-width: 768px) {
  .Header_Page .Header_Page_Cover_Left {
    flex-direction: column; } }

@media only screen and (max-width: 768px) {
  .Header_Page .Header_Page_Cover_Left a {
    margin: 5px 0 5px 0; } }

.Header_Page .Header_Page_Cover_Right {
  width: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

@media only screen and (max-width: 768px) {
  .Header_Page .Header_Page_Cover_Right {
    flex-direction: column-reverse; } }

@media only screen and (max-width: 768px) {
  .Header_Page .Header_Page_Cover_Right {
    align-items: flex-end; } }

@media only screen and (max-width: 585px) {
  .Header_Page .Header_Page_Cover_Right {
    align-items: flex-start; } }

@media only screen and (max-width: 1024px) {
  .Header_Page .Header_Page_Cover_Right a {
    margin: 0 0 0 5px; } }

@media only screen and (max-width: 768px) {
  .Header_Page .Header_Page_Cover_Right a {
    margin: 5px 0 5px 0; } }

@media only screen and (max-width: 585px) {
  .Header_Page .Header_Page_Cover_Right a {
    margin: 5px 0 5px 0; } }
